.Background {
}
.Hero {
    background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
        url("./resources/background.png");
    width: 100vw;
    height: 100vh;
    top: 0px;
    left: 0px;
    z-index: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.HeroWrapper {
    max-width: 1000px;
    margin: 20px;
}
.Title {
    font-family: "Georgia";
    font-style: italic;
    font-weight: 700;
    font-size: 60px;
    line-height: 68px;
    color: white;
    max-width: 1000px;
    margin: 0;
}
.Subtitle {
    font-family: "Georgia";
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    color: white;
}
h2 {
    font-style: normal;
    font-weight: 800;
    font-size: 30px;
    line-height: 35px;
    text-align: center;
    margin: 0;
}
h3 {
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    line-height: 29px;
    margin: 0;
}
li,
p {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 500;
}
ul {
    margin: 5px;
    padding-left: 10px;
}
.TabBar,
.content {
    max-width: 700px;
}
.content {
    display: none;
}
.active-content {
    display: block;
}
.Parts {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
}
.TabBar {
    display: flex;
    gap: 5vw;
    margin: 20px 0px 40px 0px;
}
.Tab {
    cursor: pointer;
}
.Active-Tab {
    border-style: none none solid none;
    border-color: #ef5da8;
    border-width: 4px;
}
@media (max-width: 800px) {
    .Parts {
        max-width: 400px;
        grid-template-columns: 1fr;
    }
    .Hero {
        background-attachment: scroll;
    }
}
